
var api = require('../api');
var user = require('../vm/user');
var message = require('../message');

// Component for user edit page.

function page(params) {

    var model = {

        user: ko.observable()
    };

    var tasks;

    if (params.id) {

        // Edit existing user.

        tasks = [ api.roles(), api.user(params.id) ];

        Promise.all(tasks).then(function(data) {

            var roles = data[0], userData = data[1];

            model.user(user.create(roles, userData));

        }).catch(message.error);

    } else {

        // Create a new user.

        tasks = [ api.roles() ];

        Promise.all(tasks).then(function(data) {

            var roles = data[0];

            model.user(user.create(roles));

            var fullname = document.querySelector('#user-fullname');

            fullname.focus();

            if (typeof fullname.setSelectionRange === 'function') {

                fullname.setSelectionRange(0, fullname.value.length);
            }

        }).catch(message.error);
    }

    return model;
}

ko.components.register('user', {

    viewModel: { createViewModel: page },

    template: "<!-- ko with: user -->\n    <h2 data-bind=\"text: fullname\"></h2>\n\n    <form data-bind=\"submit: save\" autocomplete=\"off\">\n        <div class=\"form-group\">\n            <label for=\"user-fullname\">Full name</label>\n            <input data-bind=\"value: fullname\"\n                type=\"text\" class=\"form-control\" id=\"user-fullname\"\n                placeholder=\"Full name\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-username\">Username</label>\n            <input data-bind=\"value: username\"\n                type=\"text\" class=\"form-control\" id=\"user-username\"\n                placeholder=\"Username\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-type\">Type</label>\n            <select data-bind=\"value: type, options: roles,\n                optionsText: 'label', optionsValue: 'name'\"\n                class=\"form-control\" id=\"user-type\"></select>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-link\">Link</label>\n            <input data-bind=\"value: link\"\n                type=\"text\" class=\"form-control\" id=\"user-link\"\n                placeholder=\"Link\">\n        </div>\n        <div class=\"checkbox\">\n            <label><input data-bind=\"checked: password_edit, disable: creating\" type=\"checkbox\">\n                Edit password</label>\n        </div>\n        <div data-bind=\"visible: password_edit\" class=\"form-group\">\n            <label for=\"user-password\">New password</label>\n            <input data-bind=\"value: password\"\n                type=\"password\" class=\"form-control\" id=\"user-password\"\n                placeholder=\"Password\">\n            <div class=\"checkbox\">\n                <label><input data-bind=\"checked: password_text\" type=\"checkbox\">\n                    Show text</label>\n            </div>\n        </div>\n        <button type=\"submit\" class=\"btn btn-default\">\n            <span class=\"glyphicon glyphicon-ok\"></span> Save\n        </button>\n        <a class=\"btn btn-default\" href=\"#users\">\n            <span class=\"glyphicon glyphicon-arrow-left\"></span> Cancel\n        </a>\n    </form>\n<!-- /ko -->\n"
});
