
var api = require('../api');
var message = require('../message');
var files_item = require('../vm/files_item');

// Component for displaying file listing in entry views.

function page(params) {

    var directory = params.directory;

    var model = {

        entry_id: params.id,

        entry_slug: params.slug,

        files: ko.observableArray([]),

        progress: ko.observable(0),

        slug_changed: params.slug_changed
    };

    // Submits the upload form and
    // performs the file upload process.

    model.upload = function() {

        var file = document.getElementById('entry-file').files[0];

        if (!file) {

            return;
        }

        model.file = file;

        var xhr = new XMLHttpRequest();

        xhr.upload.addEventListener('progress', model.progress, false);

        xhr.addEventListener('load', model.complete, false);
        xhr.addEventListener('error', model.failed, false);
        xhr.addEventListener('abort', model.aborted, false);

        xhr.open('POST', '/api/upload/' + encodeURIComponent(model.entry_id()));

        xhr.setRequestHeader('X-Key', api.apiKey());
        xhr.setRequestHeader('X-File-Name', file.name);
        xhr.setRequestHeader('Content-Type', 'application/octet-stream');

        xhr.send(file);
    };

    // Reports the upload progress.
    // FIXME only works on Firefox?

    model.progress = function(e) {

        if (e.lengthComputable) {

            model.progress(Math.round(e.loaded * 100 / e.total));
        }
    };

    // Executed then the upload process
    // is complete.

    model.complete = function(e) {

        var res = JSON.parse(e.target.responseText);

        if (res.status === 'success') {

            message.info('File "' + model.file.name +
                '" has been uploaded.');

            model.files.push(files_item.create(model.entry_slug, { name: model.file.name }));

            // This resets the file input.

            var wrap = document.getElementById('entry-file-wrap');

            wrap.innerHTML = '';
            wrap.innerHTML = '<input type="file" id="entry-file" class="form-control" placeholder="Your file">';

        } else {

            // FIXME show error in form.

            message.error(res.message);
        }
    };

    model.upload.failed = function() {

        // FIXME needs error checking.
    };

    // Executed when the upload process
    // was aborted.

    model.aborted = function() {

        // FIXME incomplete
    };

    // Removes the file.
    // Asks confirmation.

    model.remove = function(file) {

        if (confirm('Remove the file "' + file.name + '"?')) {

            api.removeFile(model.entry_id(), file.name).then(function() {

                message.info('File "' + file.name + '" has been removed.');

                model.files.remove(file);

            }).catch(message.error);
        }
    };

    if (model.entry_id()) {

        api.files(model.entry_id()).then(function(files) {

            files.sort(function(left, right) {

                return left.name === right.name ? 0 : (left.name < right.name ? -1 : 1);
            });

            model.files(files.map(function(file) {

                return files_item.create(model.entry_slug, file);

            }));

        }).catch(message.error);
    }

    return model;
}

ko.components.register('files', {

    viewModel: { createViewModel: page },

    template: "<h3>Entry files</h3>\n<!-- ko if: !entry_id() -->\n    <div class=\"alert alert-warning\" role=\"alert\">\n        To upload files you need to save the entry first.\n    </div>\n<!-- /ko -->\n<!-- ko if: entry_id() -->\n    <!-- ko if: slug_changed() -->\n        <div class=\"alert alert-warning\" role=\"alert\">\n            You have changed the entry slug. File management is\n            re-enabled after saving the entry.\n        </div>\n    <!-- /ko -->\n    <!-- ko if: !slug_changed() -->\n        <!-- ko if: files().length === 0 -->\n            <div class=\"alert alert-info\" role=\"alert\">\n                This entry has no files.\n            </div>\n        <!-- /ko -->\n        <div data-bind=\"foreach: files\" class=\"list-group\">\n            <li class=\"list-group-item post-item\">\n                <div data-bind=\"click: expand\" class=\"list-group-item-heading\">\n                    <span class=\"glyphicon glyphicon-file\"></span> <!-- ko text: name --><!-- /ko -->\n                </div>\n                <!-- ko if: expanded() -->\n                    URL: <!-- ko text: url --><!-- /ko -->\n                    <div class=\"post-controls\">\n                        <a data-bind=\"click: $parent.remove\"\n                            class=\"btn btn-default btn-sm\" href=\"#\">\n                            <span class=\"glyphicon glyphicon-edit\"></span> Remove</a>\n                        <a data-bind=\"attr: { href: url }\" target=\"_blank\"\n                            class=\"btn btn-default btn-sm\" href=\"#\"> View (in new tab)</a>\n                    </div>\n                <!-- /ko -->\n            </li>\n        </div>\n        <div class=\"row bc-controls\">\n            <div class=\"col-sm-6\">\n                <div id=\"entry-file-wrap\">\n                    <input type=\"file\" id=\"entry-file\" class=\"form-control\" placeholder=\"Your file\">\n                </div>\n            </div>\n            <div class=\"col-sm-6\">\n                <a data-bind=\"click: upload\" class=\"btn btn-default\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-upload\"></span> Upload\n                </a>\n            </div>\n        </div>\n    <!-- /ko -->\n<!-- /ko -->\n"
});
