

// Creates view model for the main menu.

function menu(params) {

    var model = {

        active: params.active,

        types: params.types
    };

    return model;
}

ko.components.register('menu', {

    viewModel: { createViewModel: menu },

    template: "<!-- ko if: active() -->\n    <div class=\"btn-group\" id=\"menu\">\n        <!-- ko foreach: types -->\n            <a data-bind=\"css: { active: $parent.active() === name },\n                attr: { href: '#entries/' + name }, text: menu_label\"\n                class=\"btn btn-default\" href=\"#\"></a>\n        <!-- /ko -->\n        <a data-bind=\"css: { active: active() === 'users' }\" class=\"btn btn-default\" href=\"#users\">Users</a>\n        <a class=\"btn btn-default\" href=\"#logout\">Logout</a>\n    </div>\n<!-- /ko -->\n"
});
