
var api = require('../api');
var message = require('../message');
var validate = require('../validate');

// Creates view model for the login page.

function page() {

    var model = {

        username: ko.observable('').trimmed(),

        password: ko.observable('').trimmed(),

        remember: ko.observable(false)
    };

    model.login = function(form) {

        validate.clear(form);

        var username = model.username();

        var password = model.password();

        if (username === '') {

            validate.error('username', 'Username is not entered.');

        } else if (!username.match(/^[^@]+@[^@]+$/)) {

            validate.error('username', 'Username must be an email.');
        }

        if (password === '') {

            validate.error('password', 'Password is not entered.');
        }

        if (validate.hasError(form)) {

            return false;
        }

        api.login(username, password).then(function(res) {

            if (model.remember()) {

                localStorage.setItem('api-key', res.key);

                sessionStorage.removeItem('api-key');

            } else {

                sessionStorage.setItem('api-key', res.key);

                localStorage.removeItem('api-key');
            }

            route.go('landing');

        }).catch(function(err) {

            validate.formError(form, err);

        });
    };

    return model;
}

ko.components.register('login', {

    viewModel: { createViewModel: page },

    template: "<h2>Login</h2>\n\n<form data-bind=\"submit: login\">\n    <div class=\"form-group\">\n        <label class=\"control-label\" for=\"username\">Username</label>\n        <input data-bind=\"textInput: username\"\n            type=\"text\" name=\"username\" title=\"Username\"\n            placeholder=\"Username\" class=\"form-control\" id=\"username\">\n    </div>\n    <div class=\"form-group\">\n        <label class=\"control-label\" for=\"password\">Password</label>\n        <input data-bind=\"textInput: password\"\n            type=\"password\" name=\"password\" title=\"Password\"\n            placeholder=\"Password\" class=\"form-control\" id=\"password\">\n    </div>\n    <div class=\"checkbox\">\n        <label><input data-bind=\"checked: remember\" type=\"checkbox\"\n            name=\"remember\" id=\"remember\">\n            Remember me</label>\n    </div>\n    <button type=\"submit\" class=\"btn btn-default\">Login</button>\n</form>\n"
});
