
var api = require('../api');
var message = require('../message');
var resolveObject = require('../resolve_object');
var comments_item = require('../vm/comments_item');

// Creates view model for the comments list.

function page(params) {

    var model = {

        title: ko.observable(),

        comments: ko.observable([])
    };

    var requests = {

        entryInfo: api.entryInfo(params.id),

        userInfo: api.userInfo(params.id),

        typeInfo: api.typeInfo(params.type),

        comments: api.comments(params.id)
    };

    resolveObject(requests).then(function(data) {

        model.title(data.entryInfo.title);

        model.comments(data.comments.map(function(comments) {

            return comments_item.create(comments, data.userInfo, data.entryInfo, data.typeInfo);

        }));

    }).catch(message.error);

    return model;
}

ko.components.register('comments', {

    viewModel: { createViewModel: page },

    template: "<h2 data-bind=\"text: title\"></h2>\n<h3>Comments</h3>\n<ul data-bind=\"foreach: comments\" class=\"list-group\">\n    <li class=\"list-group-item post-item\">\n        <h4 data-bind=\"text: author, click: expand\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Date: <span data-bind=\"text: formatDate(date)\"></span>\n            <div data-bind=\"visible: expanded\" class=\"post-controls\">\n                <div class=\"panel panel-default\">\n                    <div data-bind=\"text: content\" class=\"panel-body\"></div>\n                </div>\n                <!-- ko if: removable -->\n                    <a data-bind=\"click: remove\" class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                    </a>\n                <!-- /ko -->\n            </div>\n        </div>\n    </li>\n</ul>\n<!-- ko if: comments().length === 0 -->\n    <div class=\"alert alert-info\" role=\"alert\">\n        This entry has no comments.\n    </div>\n<!-- /ko -->\n"
});
